import faker from 'faker';
import { sample } from 'lodash';

import { useTheme } from '@material-ui/core/styles';
import { Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@material-ui/core';

import Scrollbar from '../Scrollbar';

const TABLE_HEAD = [
  { id: 'name', label: 'Name', alignRight: false },
  { id: 'userId', label: 'User Id', alignRight: false },
  { id: 'tickets', label: '# of tickets', alignRight: false },
  { id: 'brand', label: 'Brand', alignRight: false },
  { id: 'bonusName', label: 'Bonus Name', alignRight: false },
  { id: 'acceptanceState', label: 'Acceptance State', alignRight: false },
];

const winnerList = [...Array(5)].map((_, index) => {
  const setIndex = index + 1;
  return {
    userId: `id ${setIndex}`,
    name: faker.internet.userName(),
    tickets: faker.datatype.number({ min: 1000, max: 10000, precision: 1 }),
    brand: 'Brand 1',
    bonusName: '100 shoes',
    acceptanceState: sample([
      'accepted bonus ',
      'not accepted bonus',
      'accepted task not completed',
      'accepted task completed',
    ]),
  };
});

const BonusWinnersTable = () => {
  const theme = useTheme();
  return (
    <Grid container spacing={2} sx={{ mt: theme.spacing(8) }}>
      <Grid item xs={12}>
        <Typography variant="h5">Reward List</Typography>
      </Grid>
      <Grid item xs={12}>
        <Scrollbar>
          <TableContainer sx={{ minWidth: 800 }}>
            <Table aria-label="reward table">
              <TableHead>
                <TableRow>
                  {TABLE_HEAD.map((headCell) => (
                    <TableCell key={headCell.id} align={headCell.alignRight ? 'right' : 'left'}>
                      {headCell.label}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {winnerList.map((row) => (
                  <TableRow key={row.name}>
                    <TableCell>{row.name}</TableCell>
                    <TableCell>{row.userId}</TableCell>
                    <TableCell>{row.tickets}</TableCell>
                    <TableCell>{row.brand}</TableCell>
                    <TableCell>{row.bonusName}</TableCell>
                    <TableCell>{row.acceptanceState}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Scrollbar>
      </Grid>
    </Grid>
  );
};

export default BonusWinnersTable;
